import React, { useState } from 'react';
import { Input, TextArea, Select } from '../components/index.js';

export function ComponentsDemo() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    bio: '',
    country: '',
    city: '',
    experience: ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [submitted, setSubmitted] = useState(false);

  const countries = [
    { value: 'ar', label: 'Argentina' },
    { value: 'mx', label: 'Mxico' },
    { value: 'es', label: 'Espaa' },
    { value: 'co', label: 'Colombia' },
    { value: 'cl', label: 'Chile' }
  ];

  const experienceOptions = [
    { value: 'junior', label: 'Junior (0-2 aos)' },
    { value: 'mid', label: 'Middle (2-5 aos)' },
    { value: 'senior', label: 'Senior (5+ aos)' },
    { value: 'lead', label: 'Tech Lead' },
    { value: 'manager', label: 'Engineering Manager' }
  ];

  const handleInputChange = (field: keyof typeof formData) => 
    (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
      setFormData(prev => ({ ...prev, [field]: e.target.value }));
      if (errors[field]) {
        setErrors(prev => ({ ...prev, [field]: '' }));
      }
    };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.name.trim()) {
      newErrors.name = 'El nombre es requerido';
    }

    if (!formData.email.trim()) {
      newErrors.email = 'El email es requerido';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'El email no es vlido';
    }

    if (!formData.password) {
      newErrors.password = 'La contrasea es requerida';
    } else if (formData.password.length < 8) {
      newErrors.password = 'La contrasea debe tener al menos 8 caracteres';
    }

    if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = 'Las contraseas no coinciden';
    }

    if (!formData.country) {
      newErrors.country = 'Selecciona un pas';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      setSubmitted(true);
      setTimeout(() => setSubmitted(false), 3000);
      console.log('Form submitted:', formData);
    }
  };

  const getSuccessMessage = (field: keyof typeof formData) => {
    if (submitted && !errors[field] && formData[field]) {
      return ' Vlido';
    }
    return undefined;
  };

  return (
    <div className="container">
      <div className="form-container">
        <h1 className="form-title"> Componentes de Formulario</h1>
        <p style={{ textAlign: 'center', color: '#6b7280', marginBottom: '2rem' }}>
          Demostracin de Input, TextArea y Select estandarizados
        </p>

        <form onSubmit={handleSubmit}>
          {/* Seccin: Inputs bsicos */}
          <section style={{ marginBottom: '3rem' }}>
            <h2 style={{ fontSize: '1.25rem', fontWeight: 600, marginBottom: '1.5rem', color: '#1f2937' }}>
               Inputs Bsicos
            </h2>
            
            <div style={{ display: 'grid', gap: '1rem' }}>
              <Input
                label="Nombre completo"
                placeholder="Ingresa tu nombre"
                value={formData.name}
                onChange={handleInputChange('name')}
                error={errors.name}
                success={getSuccessMessage('name')}
                required
                leftIcon={<span></span>}
              />

              <Input
                type="email"
                label="Email"
                placeholder="ejemplo@correo.com"
                value={formData.email}
                onChange={handleInputChange('email')}
                error={errors.email}
                success={getSuccessMessage('email')}
                required
                leftIcon={<span></span>}
              />

              <Input
                type="tel"
                label="Telfono"
                placeholder="+54 9 11 1234-5678"
                value={formData.phone}
                onChange={handleInputChange('phone')}
                helperText="Formato: cdigo pas + nmero"
                leftIcon={<span></span>}
              />
            </div>
          </section>

          {/* Seccin: Inputs con floating labels */}
          <section style={{ marginBottom: '3rem' }}>
            <h2 style={{ fontSize: '1.25rem', fontWeight: 600, marginBottom: '1.5rem', color: '#1f2937' }}>
               Floating Labels
            </h2>
            
            <div style={{ display: 'grid', gap: '1rem' }}>
              <Input
                type="password"
                variant="floating"
                label="Contrasea"
                value={formData.password}
                onChange={handleInputChange('password')}
                error={errors.password}
                required
                rightIcon={<span></span>}
              />

              <Input
                type="password"
                variant="floating"
                label="Confirmar Contrasea"
                value={formData.confirmPassword}
                onChange={handleInputChange('confirmPassword')}
                error={errors.confirmPassword}
                success={formData.confirmPassword && !errors.confirmPassword ? ' Coinciden' : undefined}
                required
                rightIcon={<span></span>}
              />
            </div>
          </section>

          {/* Seccin: Variantes de diseo */}
          <section style={{ marginBottom: '3rem' }}>
            <h2 style={{ fontSize: '1.25rem', fontWeight: 600, marginBottom: '1.5rem', color: '#1f2937' }}>
               Variantes de Diseo
            </h2>
            
            <div style={{ display: 'grid', gap: '1rem' }}>
              <Input
                variant="outline"
                size="small"
                label="Input Outline (Pequeo)"
                placeholder="Diseo outline pequeo"
              />

              <Input
                variant="filled"
                size="large"
                label="Input Filled (Grande)"
                placeholder="Diseo filled grande"
              />
            </div>
          </section>

          {/* Seccin: TextArea */}
          <section style={{ marginBottom: '3rem' }}>
            <h2 style={{ fontSize: '1.25rem', fontWeight: 600, marginBottom: '1.5rem', color: '#1f2937' }}>
               rea de Texto
            </h2>
            
            <TextArea
              label="Biografa"
              placeholder="Cuntanos sobre ti..."
              value={formData.bio}
              onChange={handleInputChange('bio')}
              helperText="Mximo 500 caracteres"
              rows={4}
              resize="vertical"
            />

            <TextArea
              variant="floating"
              label="Descripcin del Proyecto"
              placeholder="Escribe aqu..."
              helperText="Con floating label"
              rows={3}
              resize="none"
            />
          </section>

          {/* Seccin: Select */}
          <section style={{ marginBottom: '3rem' }}>
            <h2 style={{ fontSize: '1.25rem', fontWeight: 600, marginBottom: '1.5rem', color: '#1f2937' }}>
               Selectores
            </h2>
            
            <div style={{ display: 'grid', gap: '1rem' }}>
              <Select
                label="Pas"
                options={countries}
                value={formData.country}
                onChange={handleInputChange('country')}
                placeholder="Selecciona un pas"
                error={errors.country}
                success={getSuccessMessage('country')}
                required
              />

              <Select
                variant="floating"
                label="Experiencia"
                options={experienceOptions}
                value={formData.experience}
                onChange={handleInputChange('experience')}
                placeholder="Selecciona tu nivel"
                helperText="Aos de experiencia en desarrollo"
              />

              <Select
                variant="filled"
                size="small"
                label="Ciudad"
                value={formData.city}
                onChange={handleInputChange('city')}
                disabled
                helperText="Se habilitar despus de seleccionar el pas"
              >
                <option value="">Selecciona una ciudad</option>
                <option value="bsas">Buenos Aires</option>
                <option value="cdmx">Ciudad de Mxico</option>
                <option value="madrid">Madrid</option>
              </Select>
            </div>
          </section>

          {/* Botones de accin */}
          <div className="form-actions" style={{ marginTop: '2rem' }}>
            <button type="submit" className="btn btn-primary">
               Guardar Informacin
            </button>
            <button type="button" className="btn btn-outline" onClick={() => {
              setFormData({
                name: '',
                email: '',
                password: '',
                confirmPassword: '',
                phone: '',
                bio: '',
                country: '',
                city: '',
                experience: ''
              });
              setErrors({});
            }}>
               Limpiar Formulario
            </button>
          </div>

          {submitted && (
            <div style={{
              marginTop: '1rem',
              padding: '1rem',
              backgroundColor: '#f0fdf4',
              border: '2px solid var(--color-green)',
              borderRadius: 'var(--radius)',
              color: 'var(--color-green)',
              textAlign: 'center',
              fontWeight: 500
            }}>
               Formulario enviado exitosamente
            </div>
          )}
        </form>
      </div>

      {/* Informacin tcnica */}
      <div style={{ 
        marginTop: '3rem', 
        padding: '1.5rem', 
        backgroundColor: '#f8fafc', 
        borderRadius: 'var(--radius)',
        border: '1px solid #e2e8f0'
      }}>
        <h3 style={{ fontSize: '1.125rem', fontWeight: 600, marginBottom: '1rem', color: '#1f2937' }}>
           Caractersticas de los Componentes
        </h3>
        <ul style={{ color: '#6b7280', lineHeight: 1.6 }}>
          <li><strong>Input:</strong> Soporte para iconos, mltiples variantes (default, floating, outline, filled), estados de validacin</li>
          <li><strong>TextArea:</strong> Redimensionamiento controlado, etiquetas flotantes, mismo sistema de validacin</li>
          <li><strong>Select:</strong> Flecha personalizada, opciones como array o children, estados consistentes</li>
          <li><strong>Accesibilidad:</strong> ARIA labels, IDs nicos automticos, navegacin por teclado</li>
          <li><strong>Responsive:</strong> Adaptable a diferentes tamaos de pantalla</li>
        </ul>
      </div>
    </div>
  );
}
